@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
:root {
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  text-rendering: optimizeLegibility;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

.header,
.main {
  padding: 0 1rem;
}

.header {
  margin-top: 7.5rem;
}

@media (max-width: 696px) {
  .header {
    margin-top: 6rem;
  }
}

.main {
  margin-top: 2rem;
}

.container {
  min-height: 100vh;
  background-color: #161819;
  display: flex;
  flex-direction: column;
}

.mobile-nav {
  position: fixed;
  inset: 0;
  z-index: 100;
  background-color: #161819;
  opacity: 0.9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  -webkit-backdrop-filter: blur(16px);
          backdrop-filter: blur(16px);
  transition: -webkit-transform 0.5s ease-out;
  transition: transform 0.5s ease-out;
  transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
}

.mobile-nav.closed {
  transition-timing-function: ease-in;
  -webkit-transform: translate3d(0, -100vh, 0);
          transform: translate3d(0, -100vh, 0);
}

.mobile-nav.open {
  -webkit-transform: none;
          transform: none;
}

.mobile-nav__list {
  display: flex;
  flex-direction: column;
  width: 100%;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  margin-top: -7rem;
}

.mobile-nav__list .nav-link {
  position: relative;
  text-align: center;
  font-size: 1.25rem;
  font-weight: bold;
  color: #fff;
  padding: 1.5rem 0;
}

.mobile-nav__list .nav-link:hover {
  background-color: #3f3a3a;
}

.mobile-nav__list .nav-link:last-child::after {
  display: block;
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  left: 50%;
  margin-top: 0.5rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #fff;
}

.mobile-nav__buttons {
  position: absolute;
  bottom: 2rem;
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.mobile-nav__buttons .btn {
  font-family: inherit;
  font-weight: bold;
  font-size: 0.9rem;
  background-color: transparent;
  color: #fff;
  outline: none;
  border: 1px solid #ffffff2c;
  border-radius: 8px;
  padding: 0.75rem 1.1rem;
  width: 150px;
  cursor: pointer;
}

.mobile-nav__buttons .btn:hover {
  background-color: #ffffff2c;
}

.mobile-nav__buttons .icon {
  cursor: pointer;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  background-color: #323435;
}

.mobile-nav__buttons .icon:hover {
  background-color: transparent;
}

.mobile-nav__buttons .icon.facebook {
  padding: 0.1rem;
  color: #3481f0;
}

.toggle {
  position: fixed;
  top: 2rem;
  right: 1.25rem;
  z-index: 150;
}

.toggle__icon {
  height: 30px;
  width: 30px;
  color: #fff;
  cursor: pointer;
}

.navbar {
  position: fixed;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 1.5rem 3rem 1.5rem 5rem;
}

@media (max-width: 1024px) {
  .navbar {
    padding: 1.5rem 1.25rem;
  }
}

.navbar.scrolled {
  background-color: #0c0c0c;
}

.navbar__logo {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  background-color: rgb(251, 255, 0);
  width: 45px;
  height: 45px;
  border-radius: 8px;
  padding-right: 4px;
  cursor: pointer;
  z-index: 150;
}

.navbar__logo span {
  font-size: 1.2rem;
  font-weight: bold;
}

.navbar .nav {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 696px) {
  .navbar .nav {
    display: none;
  }
}

.nav__list {
  margin-left: 1.5rem;
}

@media (max-width: 1024px) {
  .nav__list {
    margin-left: 0.5rem;
  }
}

.nav__list .nav-link {
  cursor: pointer;
  padding: 0.75rem 1rem;
  color: #fff;
  font-size: 0.9rem;
  font-weight: bold;
  margin: 0 0.25rem;
}

@media (max-width: 1024px) {
  .nav__list .nav-link {
    padding: 0.75rem 0.75rem;
  }
}

.nav__list .nav-link:hover {
  background-color: #3f3a3a;
}

.nav__list .nav-link:last-child::after {
  display: inline-block;
  content: "";
  width: 0;
  height: 0;
  margin-left: 5px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid #fff;
}

.nav__buttons {
  display: flex;
  align-items: center;
}

.nav__buttons .btn {
  font-family: inherit;
  font-weight: bold;
  font-size: 0.9rem;
  background-color: transparent;
  color: #fff;
  outline: none;
  border: 1px solid #ffffff2c;
  border-radius: 8px;
  padding: 0.75rem 1.1rem;
  margin-right: 2rem;
  cursor: pointer;
}

@media (max-width: 1024px) {
  .nav__buttons .btn {
    font-size: 0.8rem;
    margin-right: 0.75rem;
  }
}

.nav__buttons .btn:hover {
  background-color: #ffffff2c;
}

.nav__buttons .icon {
  cursor: pointer;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  margin-left: 0.5rem;
  background-color: #323435;
}

@media (max-width: 1024px) {
  .nav__buttons .icon {
    height: 35px;
    width: 35px;
  }
}

.nav__buttons .icon:hover {
  background-color: transparent;
}

.nav__buttons .icon.facebook {
  padding: 0.1rem;
  color: #3481f0;
}

.header .bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header .bottom .title,
.header .bottom .description {
  margin: 0;
  text-align: center;
  color: #fff;
}

.header .bottom .title {
  width: 100%;
  margin: 1.5rem 0;
  font-size: 2.5rem;
}

@media (max-width: 696px) {
  .header .bottom .title {
    font-size: 2rem;
  }
}

.header .bottom .description {
  opacity: 0.6;
  margin-bottom: 1.5rem;
  width: 350px;
  font-size: 0.9rem;
}

.header .bottom .searchBar {
  display: flex;
  align-items: center;
  background-color: #323435;
  width: 525px;
  padding: 0.4rem;
  border-radius: 10px;
}

@media (max-width: 696px) {
  .header .bottom .searchBar {
    width: 325px;
  }
}

.header .bottom .searchBar .filter {
  color: #fff;
  background-color: #fe638d;
  font-weight: bold;
  font-size: 0.8rem;
  padding: 0.55rem 0.4rem;
  border-radius: 5px;
  cursor: pointer;
}

.filter::after {
  display: inline-block;
  content: "";
  width: 0;
  height: 0;
  margin-left: 5px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid #fff;
}

.header .bottom .searchBar input {
  flex: 1 1;
  position: relative;
  color: #fff;
  background-color: transparent;
  outline: none;
  border: none;
  margin-left: 1rem;
}

.header .bottom .searchBar .searchIcon {
  color: #fff;
  opacity: 0.6;
  margin-right: 1rem;
  height: 20px;
  width: 20px;
}

.optionsBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3.5rem;
}

@media (max-width: 696px) {
  .optionsBar {
    padding: 0;
  }
}

.optionsBar .filterOptions {
  display: flex;
  align-items: center;
  grid-gap: 1.75rem;
  gap: 1.75rem;
}

@media (max-width: 696px) {
  .optionsBar .filterOptions {
    grid-gap: 0.75rem;
    gap: 0.75rem;
  }
}

.optionsBar .filterOptions * {
  color: #fff;
}

.optionsBar .filterOptions li {
  cursor: pointer;
  font-weight: bold;
  padding: 0.5rem 0.25rem;
}

.optionsBar .filterOptions .latest {
  position: relative;
}

@media (max-width: 696px) {
  .optionsBar .filterOptions .latest,
  .optionsBar .filterOptions .popular {
    font-size: 0.9rem;
  }
}

@media (max-width: 360px) {
  .optionsBar .filterOptions .latest,
  .optionsBar .filterOptions .popular {
    font-size: 0.8rem;
  }
}

.optionsBar .filterOptions .latest::before {
  content: "";
  position: absolute;
  right: -2px;
  top: 6px;
  height: 6px;
  width: 6px;
  background-color: #fa4f02;
  border-radius: 50%;
}

.optionsBar .filterOptions .divisor {
  opacity: 0.8;
}

.optionsBar .filterOptions .premium,
.optionsBar .filterOptions .free {
  font-size: 0.9rem;
}

@media (max-width: 696px) {
  .optionsBar .filterOptions .premium,
  .optionsBar .filterOptions .free {
    font-size: 0.8rem;
  }
}

@media (max-width: 360px) {
  .optionsBar .filterOptions .premium,
  .optionsBar .filterOptions .free {
    font-size: 0.75rem;
  }
}

.optionsBar .filterOptions .premium {
  color: #a74212;
}

.optionsBar .filterOptions .free {
  color: #3969ab;
}

.optionsBar .settings {
  display: flex;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

@media (max-width: 696px) {
  .optionsBar .settings {
    grid-gap: 1rem;
    gap: 1rem;
  }
}

.optionsBar .settings svg {
  cursor: pointer;
  fill: #828485;
}

@media (max-width: 696px) {
  .optionsBar .settings svg {
    height: 20px;
    width: 20px;
  }
}

.presentation__title,
.presentation__description {
  margin: 0;
  text-align: center;
  color: #fff;
}

.presentation__title {
  width: 100%;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
}

@media (max-width: 696px) {
  .presentation__title {
    font-size: 2rem;
  }
}

.presentation__description {
  opacity: 0.6;
  margin-bottom: 1.5rem;
  margin-left: auto;
  margin-right: auto;
  width: 350px;
  font-size: 0.9rem;
}

@media (max-width: 390px) {
  .presentation__description {
    width: 90vw;
  }
}

.presentation__searchBar {
  display: flex;
  align-items: center;
  background-color: #323435;
  width: 525px;
  margin: 0 auto;
  padding: 0.4rem;
  border-radius: 10px;
}

@media (max-width: 696px) {
  .presentation__searchBar {
    width: calc(325px + 5vw);
  }
}

@media (max-width: 696px) {
  .presentation__searchBar {
    width: 90vw;
  }
}

.searchBar__filter {
  color: #fff;
  background-color: #fe638d;
  font-weight: bold;
  font-size: 0.8rem;
  padding: 0.55rem 0.4rem;
  border-radius: 5px;
  cursor: pointer;
}

.searchBar__filter::after {
  display: inline-block;
  content: "";
  width: 0;
  height: 0;
  margin-left: 5px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid #fff;
}

.presentation__searchBar input {
  flex: 1 1;
  position: relative;
  color: #fff;
  background-color: transparent;
  outline: none;
  border: none;
  margin-left: 1rem;
}

.presentation__searchBar .search-icon {
  color: #fff;
  opacity: 0.6;
  margin-right: 1rem;
  height: 20px;
  width: 20px;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 350px);
  justify-content: center;
  padding: 0 2rem;
  grid-gap: 2rem;
  gap: 2rem;
  margin-top: 1rem;
  margin-bottom: 2.5rem;
}

@media (max-width: 1024px) {
  .list {
    padding: 0 1rem;
  }
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1e2021;
  height: 400px;
  width: 350px;
  border-radius: 6%;
  box-shadow: -10px 10px 10px 5px rgba(0, 0, 0, 0.25);
  -webkit-transform: scale3d(0);
          transform: scale3d(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

@media (max-width: 696px) {
  .card {
    margin: 0 auto;
    width: 100%;
  }
}

@media (max-width: 380px) {
  .card {
    width: 90%;
  }
}

.card:hover {
  box-shadow: -25px 25px 20px 5px rgba(0, 0, 0, 0.25);
  -webkit-transform: scale3d(105%, 105%, 105%);
          transform: scale3d(105%, 105%, 105%);
}

.card .vectorImage {
  background-color: #333438;
  width: 90%;
  height: 60%;
  border-radius: 6%;
  margin-top: 1.1rem;
}

.card .vectorImage .type {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  padding: 0.5rem;
  margin: 0 0.75rem;
}

.card .vectorImage .type .icon {
  fill: #a45b64;
  height: 18px;
  height: 18px;
}

.card .vectorImage .type span {
  color: #909195;
  font-size: 0.8rem;
  font-weight: bold;
}

.card .vectorImage .type .icon.free {
  fill: #328683;
}

.card .vectorImage .imageContainer .image {
  width: 100%;
  height: 200px;
  object-fit: contain;
}

.card .title {
  color: #e3e3e3;
  text-align: center;
  margin-top: 0.65rem;
  font-size: 0.9rem;
  font-weight: 300;
}

.card .title h2 {
  margin-top: 0;
}

.card .title .highlight {
  color: #b1575e;
  font-size: 2.75rem;
  font-weight: bold;
}

.card .download {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.card .download button {
  background-color: transparent;
  outline: none;
  border: none;
  text-decoration: underline;
  color: #7d7f81;
  font-size: 1.75rem;
  font-weight: bold;
  cursor: pointer;
}

.card .download .icon {
  fill: #3780da;
  cursor: pointer;
}

.footer {
  position: relative;
  left: -2.5%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: auto;
  margin-bottom: 2rem;
}

@media (max-width: 1024px) {
  .footer {
    left: 0;
    padding: 0 2rem;
    justify-content: space-between;
  }
}

@media (max-width: 696px) {
  .footer {
    justify-content: initial;
    flex-direction: column-reverse;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

.footer .discover {
  display: flex;
  align-items: center;
}

.footer .discover .link {
  color: #7d7f81;
  font-weight: bold;
  font-size: 1.1rem;
  line-height: 1.5;
  width: 150px;
  text-decoration: underline;
  cursor: pointer;
}

@media (max-width: 1024px) {
  .footer .discover .link {
    font-size: 1rem;
    width: 120px;
  }
}

.footer .discover .collections {
  width: 150px;
  height: 75px;
  border-radius: 10px;
  display: flex;
  align-items: center;
}

@media (max-width: 1024px) {
  .footer .discover .collections {
    width: 125px;
    height: 50px;
  }
}

.footer .discover .collections .collection {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0.1rem;
  background-color: rgba(255, 255, 255, 0.801);
  border-radius: 10px;
}

.footer .discover .collections .collection:nth-child(2) {
  left: -20px;
}

.footer .discover .collections .collection:nth-child(3) {
  left: -40px;
}

.footer .discover .collections .collection:nth-child(4) {
  left: -60px;
}

.footer .discover .collections .collection img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  background-color: #fff;
  object-fit: cover;
}

@media (max-width: 1024px) {
  .footer .discover .collections .collection img {
    width: 45px;
    height: 45px;
  }
}

.footer button {
  position: relative;
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  background-color: #323337;
  border: none;
  outline: none;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  font-family: inherit;
  cursor: pointer;
  padding: 1.5rem 2rem;
  height: 40px;
  border-radius: 5px;
}

@media (max-width: 696px) {
  .footer button {
    margin-bottom: 2rem;
  }
}

.footer button .icon {
  fill: #74767a;
}

.footer .pageControl {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.footer .pageControl .label,
.footer .pageControl .page {
  color: #5f676c;
  font-weight: bold;
  font-size: 0.9rem;
}

.footer .pageControl .controls {
  display: flex;
  grid-gap: 0.25rem;
  gap: 0.25rem;
}

.footer .pageControl .control {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1f2123;
  height: 25px;
  width: 25px;
  border-radius: 5px;
}

.footer .pageControl .control .icon {
  fill: #fff;
  height: 20px;
  width: 20px;
  cursor: pointer;
}

.footer .pageControl .control:nth-child(2) {
  background-color: #393a3e;
}

.footer .pageControl .control span {
  color: #fff;
  font-weight: bold;
  font-size: 0.8rem;
}

